<template>
	<yd-layout>
		    <yd-navbar slot="navbar" title="我的日常">
				<a @click="goToIndex" slot="left">
					<yd-navbar-back-icon></yd-navbar-back-icon>
				</a>
		    </yd-navbar>
		    <div class="list-container">
		    	<div class="list-item" v-for="item in list">
			    	<div class="title">
						{{item.name}}:
			    	</div>
			    	<ul>
			    		<li  v-for="(bo,index) in item.childs" :key="item.MENU_ID_">
			    			<span class="list-icon">
			    				<i class="yd-icon-mysolutions"></i>
			    			</span>
			    			<router-link class="link-a" :to="{name:'customList',params:{alias:bo.KEY_}}">{{bo.NAME_}}</router-link>
			    		</li>
			    	</ul>
			    </div>	
		    </div>
		    <rx-nodata :amount="list.length"></rx-nodata>
    </yd-layout>
</template>

<script>
import Vue from 'vue'
import NoData from "@/components/common/NoData";

Vue.component(NoData.name, NoData);


export default{
	name:"myCustomList",
	data(){
		return {
			list:[]
		}
	},
	created(){
		var url=_baseUrl +"/mobile/custom/getMyMobileList.do";
		this.$ajax.get(url).then(res=>{
			this.list=res.data;
		})
	}
}
</script>

<style scoped>
	.list-container{
		box-sizing: border-box;
		width: 100%;
	}
	
	.list-item{
		background: #fff;
		padding: 0 .12rem;
	}
	
	.list-item .title{
		/*height: .7rem;*/
		/*font-weight: bold;*/
		/*padding-left: .2rem;*/
		font-size: .28rem;
		color: #999;
		padding-top: .12rem;
	}
	
	.list-item ul{
		/*padding-left: .4rem;*/
		/*font-size: .25rem;*/
	}
	
	.list-item li{
		/*height: .5rem;*/
		padding: .1rem 0;
		border-bottom: 1px solid #ececec;
		width: 100%;
		display: flex;
		align-items: center;
	}
	
	.list-item li:last-child{
		border-bottom: none;
	}
	
	.list-item li .list-icon{
		padding: .1rem;
		border-radius: .12rem;
		display: flex;
		align-items: center;
		justify-content: center;
		height: .8rem;
		width: .8rem;
	}
	
	.list-item li:nth-of-type(1) .list-icon{
		background: #26deef;
	}

	.list-item li:nth-of-type(2) .list-icon{
		background: #ffc17c;
	}
	.list-item li:nth-of-type(3) .list-icon{
		background: #28efb4;
	}
	.list-item li:nth-of-type(4) .list-icon{
		background: #f9906f;
	}
	.list-item li:nth-of-type(5) .list-icon{
		background: #ff8292;
	}
	.list-item li:nth-of-type(6) .list-icon{
		background: #ffc17c;
	}
	.list-item li:nth-of-type(7) .list-icon{
		background: #75d98a;
	}
	.list-item li:nth-of-type(8) .list-icon{
		background: #6fc7f8;
	}
	.list-item li:nth-of-type(9) .list-icon{
		background: #75d98a;
	}
	.list-item li:nth-of-type(10) .list-icon{
		background: #6fc7f8;
	}
	
	
	
	.list-item li .list-icon i{
		font-size: .5rem;
		color: #fff;
	}
	
	.link-a{
		font-size: .3rem;
		color: #666;
		margin-top: -.02rem;
		padding: .12rem;
		flex-grow: 1;
	}
	
</style>


